window.document.addEventListener('scroll', () => {
  let navLinks = document.querySelectorAll('.nav__link')  

  if (window.scrollY >= 80 || window.pageYOffset >= 80){
    document.querySelector('.header').classList.add('inverted');
    document.querySelector('.nav__logo').classList.add('nav__inverted');
    navLinks.forEach(link => {
      link.classList.add('nav__inverted');
    });
  } else {
    document.querySelector('.header').classList.remove('inverted');
    document.querySelector('.nav__logo').classList.remove('nav__inverted');
    navLinks.forEach(link => {
      link.classList.remove('nav__inverted');
    });
  }
});